import styled from "styled-components";
import { Text, Title1, Title3 } from "../../assets/styles/component";
import Tag from "../Intro/tag";
import arrow_up_right from "../../assets/images/arrow-up-right.svg";

const STask = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  gap: 50px;

  padding: 40px;
  width: calc(50% - 100px);
  text-align: left;

  border: 2px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.05);
  border-radius: 28px;

  transition: 0.5s;

  &:hover {
    background: rgba(255, 255, 255, 0.15);
  }

  & p {
    line-height: 150%;
  }

  & h3 {
    font-size: clamp(48px, 3vw, 60px);
    line-height: 120%;
    margin-bottom: 24px;
  }
`;

const TagsList = styled.div`
  display: flex;
  gap: 8px;

  & div {
    position: relative;
    transition: 0.25s ease-in-out;
  }

  & div:hover {
    padding-right: 45px;
  }
  & div::after {
    opacity: 0%;
    transition: 1.5s;
  }

  & div:hover::after {
    position: absolute;
    right: 16px;
    top: 12px;
    content: "";
    width: 22px;
    height: 20px;
    opacity: 100%;
    background-image: url(${arrow_up_right});
  }
`;

const Title = styled(Title3)`
  line-height: 130%;
`;

const Task = ({ title, desc, tags }) => {
  return (
    <STask>
      <div>
        <Title weight={300}>{title}</Title>
        <Text size={20}>{desc}</Text>
      </div>
      <TagsList>
        {tags.map((tag) => (
          <Tag key={Math.random()}>{tag}</Tag>
        ))}
      </TagsList>
    </STask>
  );
};

export default Task;
