import { useState } from "react";
import { useEffect, useRef } from "react";
import styled from "styled-components";
import { EngLoc } from "../../assets/constants/localizations";
import { dribble_right, smooth_right } from "../../assets/styles/animations";
import { Title2, Title3 } from "../../assets/styles/component";
import Task from "./taskDesktop";

const STasks = styled.section`
  padding: 150px 0 180px 0;

  background: radial-gradient(
      105.94% 105.94% at 113.83% -33.36%,
      #ff7cda 0%,
      rgba(255, 124, 218, 0) 100%
    ),
    radial-gradient(
      45.01% 95.17% at 134.48% 0%,
      #7400e8 0%,
      rgba(116, 0, 232, 0) 100%
    ),
    radial-gradient(
      40.65% 44.52% at 90.65% 94.48%,
      #00aee8 0%,
      rgba(0, 174, 232, 0) 100%
    ),
    radial-gradient(
      54.92% 55.79% at 45.91% 41.36%,
      rgba(0, 174, 232, 0.91) 0%,
      rgba(0, 174, 232, 0) 100%
    ),
    radial-gradient(
      56.31% 80.35% at 14.82% 95.16%,
      #7400e8 0%,
      rgba(116, 0, 232, 0) 100%
    ),
    radial-gradient(
      47.68% 95.44% at -2.01% -21.74%,
      #00aee8 0%,
      rgba(0, 174, 232, 0) 100%
    ),
    #000f67;
`;

const TasksInner = styled.div`
  display: flex;
  flex-direction: column;
  gap: 120px;
  margin: 0 15%;

  @media (max-width: 1200px) {
    width: calc(100% - 10%);
    margin: auto;
  }
`;

const TaskList = styled.div`
  display: flex;
  gap: 20px;
  flex-wrap: wrap;

  & > div {
    opacity: 0;
    ${({view}) => !view && dribble_right}
  }
`;

const Tasks = ({ language }) => {
  const [view, setView] = useState(true);

  const intersectionObserver = new IntersectionObserver((entries) => {
    if (entries[0].intersectionRatio <= 0) return;
    setView(false);
  });
  
  useEffect(() => {
    intersectionObserver.observe(elem.current && elem.current);
  }, []);

  const elem = useRef(null);
  
  return (
    <STasks ref={elem}>
      <TasksInner>
        <Title2>
          {language === "Ru" ? "Какие задачи решаем" : EngLoc.tasks.title}
        </Title2>
        <TaskList view={view}>
          <Task
            title={
              language === "Ru"
                ? "Медийная реклама"
                : EngLoc.tasks.tasks_list[0].title
            }
            desc={
              language === "Ru"
                ? "Вывод бренда на рынок, увеличение узнаваемости и тп"
                : EngLoc.tasks.tasks_list[0].desc
            }
            tags={["TeleJet", "RocketVideo"]}
          />
          <Task
            title={
              language === "Ru" ? "Конверсии" : EngLoc.tasks.tasks_list[1].title
            }
            desc={
              language === "Ru"
                ? "Достижение целей на сайтах и продажи"
                : EngLoc.tasks.tasks_list[1].desc
            }
            tags={["RocketVideo"]}
          />
          <Task
            title={
              language === "Ru"
                ? "Раскрутка соц.сетей бизнеса"
                : EngLoc.tasks.tasks_list[2].title
            }
            desc={
              language === "Ru"
                ? "Увеличение кол-ва подписчиков во всех соц.сетях"
                : EngLoc.tasks.tasks_list[2].desc
            }
            tags={["SocialJet"]}
          />
          <Task
            title={
              language === "Ru" ? "Аналитика" : EngLoc.tasks.tasks_list[3].title
            }
            desc={
              language === "Ru"
                ? "Анализ источников тарифа и рекламных кампаний"
                : EngLoc.tasks.tasks_list[3].desc
            }
            tags={["TeleJet", "RocketVideo", "SocialJet"]}
          />
        </TaskList>
      </TasksInner>
    </STasks>
  );
};

export default Tasks;
